//
// docs.scss
//

:not(pre)>code[class*=language-], pre[class*=language-] {
    background: $white;
    border: 1px solid rgba(152,166,173,.2);
    border-top: 1px solid transparent;
}

.example-container {
    background: $gray-100;
    padding: 3rem;
}

.hljs {
    background: $white !important;
    padding: 2rem !important;
    border: 1px solid rgba($gray-600,0.2);
    max-height: 420px;
    overflow-y: auto;
}

.highlight {
    margin-top: -25px;
}

.width-40 {
    width: 40%;
    margin: 0 auto;
}

.width-60 {
    width: 60%;
    margin: 0 auto;
}
