// 
// topbar.scss
//
.navbar-custom {
    padding: 20px 40px 20px 40px;
    background-color: $bg-topbar;
    box-shadow: $shadow;
    margin: -5px -30px 0;
    .topbar-left {
        background-color: $bg-topbar;
        height: 70px;
        position: fixed;
        z-index: 1;
        width: $leftbar-width;
        text-align: center;
        top: 0;
        left: 0;
        .logo {
            line-height: 70px;
            i {
                display: none;
            }
        }
    }
}