@media (min-width: 680px) {
    .page-wrapper {
        display: flex;

        .left-sidebar {
            min-width: 250px;
            max-width: 250px;
            padding-bottom: 30px;
        }
        .page-content {
            margin-left: 30px;
            width: 100%;
        }
    }
}

.content-page {
    margin-left: 0;
    overflow: hidden;
    padding: 5px 30px;
    min-height: 80vh;
}

.list-group-item {
    border-radius: 3px;
}

.list-group-item.disabled, .list-group-item:disabled {
    background-color: transparent;
    font-size: 13px;
    font-weight: 700;
    color: $primary;
}