// Metis Menu Overwrite

.metismenu {
    padding: 0;
    ul {
        padding: 0;
        li {
            list-style: none;
        }
    }
}

.nav-second-level,
.nav-thrid-level {
    li {
        a {
            padding: 8px 20px 8px 10px;
            color: $menu-item;
            display: block;
            position: relative;
            transition: all 0.4s;
            &:focus,
            &:hover {
                background-color: $bg-leftbar;
                color: $menu-item-hover;
            }
        }
    }
}

.nav-second-level {
    >li {
        >a {
            padding-left: 68px;
        }
    }
    li.active {
        >a {
            color: $menu-item-active;
        }
    }
}

.nav-third-level {
    >li {
        >a {
            padding-left: 78px;
        }
    }
    li.active {
        >a {
            color: $menu-item-active;
        }
    }
}
