// 
// pricing.scss
//


.price-card {
    position: relative;
    
    .pricing-plan-name {
        padding-bottom: 20px;
    }

    .pricing-icon {
        font-size: 22px;
        box-shadow: $shadow;
        height: 60px;
        display: inline-block;
        width: 60px;
        line-height: 68px;
        border-radius: 50%;
    }

    .pricing-price {
        padding: 30px 0 0;

        span {
            font-size: 40%;
            color: $gray-600;
            letter-spacing: 2px;
            text-transform: uppercase;
        }
    }
    .pricing-features {
        color: $gray-600;
        list-style: none;
        margin: 0;
        padding: 20px 0 0 0;

        li {
            padding: 15px;
        }
    }
}

.recommended-plan {
    margin-top: -1.9375rem;

    .recommended-plan-tag {
        background-color: rgba($danger,0.2);
        color: $danger;
        padding: 5px 0;
        font-weight: 700;
        border-radius: .25rem .25rem 0 0;
        margin: -1.5rem -1.5rem 1.5rem -1.5rem;
    }
}